.col-aside {
  @include col();
  .contacts {
    img {
      fill: #94d31b;
      width: .400rem;
    }

    ul {
      list-style: disc
    }
  }

  .languages {
    ul {
      list-style: disc;

      li {
        span {
          font-weight: 600;
        }
      }
    }
  }

  .education {
    li {
      list-style: disc;
    }
  }

  .certificates {

    a {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 1rem;
        height: 1rem;
        top: 3px;
        left: -25px;
        background: url(../images/svg/arrow-forward-outline.svg) no-repeat center center;
        background-size: 1rem;

      }
    }
  }
}
