* {
  box-sizing: border-box;
}

// html {
//   @include fluid-type(font-size, $minScreen, $maxScreen, $minFont, $maxFont);
// }

body {
  font-family: 'Roboto', sans-serif;
  color: #333;
  background-color: #F6F5F1;
}

section {
  margin-bottom: 50px;

  @include to($max-width: 425px) {
    margin-bottom: 25px;
  }
}

p {
  line-height: 2rem;
}

h3 {
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
}

ul {
  list-style: none;
  margin: 0;
}

li {
  line-height: 2rem;

}

a {
  color: #48A3B8;
  text-decoration: none;
  transition: color 0.2s;

  &:hover {
    color: $black-bright;
  }
}

.container {
  @include wrapper();
  overflow: hidden;

  .row {
    @include row-flex();

    .col-aside {
      @include col();
      @include size(4);
      @include size-xs(12);
    }

    .col-main {
      @include col();
      @include size(8);
      @include size-xs(12);

    }

    .title-description {
      position: relative;
      &::before {
        content:'';
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: -.20rem;
        background-color: $black-bright;
      }
    }
  }
}
