.footer {
  height: 100px;
  background-color: #363636;

  .container {
    height: 100%;
  }

  ul {
    height: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    justify-content: center;
    align-items: center;

    list-style: none;
    padding: 0;
    margin: 0;



    li {
      width: 33.33%;
      text-align: center;

      &:nth-child(1) {
        border-left: 1px solid #fff;

        @include to($max-width: 425px) {
          border: none;
        }
      }
      &:nth-child(2) {
        border-left: 1px solid #fff;
        border-right: 1px solid #fff;

        @include to($max-width: 425px) {
          border: none;
        }
      }

      &:nth-child(3) {
        border-right: 1px solid #fff;

        @include to($max-width: 425px) {
          border: none;
        }
      }

      .icon-link {
        display: none;

        @include to($max-width: 425px) {
          display: block;

        }
        .linkedin {
          display: inline-block;
          width: 3rem;
          height: 3rem;
          background: url(../images/svg/logo-linkedin.svg) no-repeat;
          background-size: 3rem;
        }

        .github {
          display: inline-block;
          width: 3rem;
          height: 3rem;
          background: url(../images/svg/logo-octocat.svg) no-repeat;
          background-size: 3rem;
        }

        .facebook {
          display: inline-block;
          width: 3rem;
          height: 3rem;
          background: url(../images/svg/logo-facebook.svg) no-repeat;
          background-size: 3rem;
        }

      }

      .text-link {
        color: #fff;
        transition: color 0.2s;

        @include to($max-width: 425px) {
          display: none;
        }

        &:hover {
          color: #9b9b9b;
        }
      }
    }
  }
}
