* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: #333;
  background-color: #F6F5F1;
}

section {
  margin-bottom: 50px;
}

@media screen and (max-width: 425px) {
  section {
    margin-bottom: 25px;
  }
}

p {
  line-height: 2rem;
}

h3 {
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
}

ul {
  list-style: none;
  margin: 0;
}

li {
  line-height: 2rem;
}

a {
  color: #48A3B8;
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  color: #363636;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.container .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.container .row .col-aside {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.3333333333% - 30px);
}

@media screen and (max-width: 425px) {
  .container .row .col-aside {
    width: calc(100% - 30px);
  }
}

.container .row .col-main {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(66.6666666667% - 30px);
}

@media screen and (max-width: 425px) {
  .container .row .col-main {
    width: calc(100% - 30px);
  }
}

.container .row .title-description {
  position: relative;
}

.container .row .title-description::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: -.20rem;
  background-color: #363636;
}

.header {
  margin: 60px 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

@media screen and (max-width: 768px) {
  .header {
    justify-content: center;
  }
}

.header .portfolio {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.6666666667% - 30px);
}

@media screen and (max-width: 960px) {
  .header .portfolio {
    width: calc(100% - 30px);
  }
}

.header .title {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.6666666667% - 30px);
  border: 3px solid #363636;
  width: 100%;
  max-width: 500px;
  text-align: center;
}

@media screen and (max-width: 960px) {
  .header .title {
    width: calc(100% - 30px);
  }
}

.header .title .name {
  position: relative;
  margin-bottom: 30px;
  font-weight: 500;
}

.header .title .name::before {
  content: '';
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  width: 50px;
  height: 1px;
  bottom: -.70rem;
  background-color: #363636;
}

.header .title .profession {
  font-weight: 400;
  text-transform: capitalize;
  letter-spacing: 0px;
  font-style: italic;
}

.col-aside {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.col-aside .contacts img {
  fill: #94d31b;
  width: .400rem;
}

.col-aside .contacts ul {
  list-style: disc;
}

.col-aside .languages ul {
  list-style: disc;
}

.col-aside .languages ul li span {
  font-weight: 600;
}

.col-aside .education li {
  list-style: disc;
}

.col-aside .certificates a {
  position: relative;
}

.col-aside .certificates a::before {
  content: "";
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 3px;
  left: -25px;
  background: url(../images/svg/arrow-forward-outline.svg) no-repeat center center;
  background-size: 1rem;
}

.col-main {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.col-main li {
  list-style: disc;
}

.col-main .items-skills {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.col-main .items-skills .item {
  width: 49%;
}

@media screen and (max-width: 480px) {
  .col-main .items-skills .item {
    width: 100%;
  }
}

.footer {
  height: 100px;
  background-color: #363636;
}

.footer .container {
  height: 100%;
}

.footer ul {
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer ul li {
  width: 33.33%;
  text-align: center;
}

.footer ul li:nth-child(1) {
  border-left: 1px solid #fff;
}

@media screen and (max-width: 425px) {
  .footer ul li:nth-child(1) {
    border: none;
  }
}

.footer ul li:nth-child(2) {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}

@media screen and (max-width: 425px) {
  .footer ul li:nth-child(2) {
    border: none;
  }
}

.footer ul li:nth-child(3) {
  border-right: 1px solid #fff;
}

@media screen and (max-width: 425px) {
  .footer ul li:nth-child(3) {
    border: none;
  }
}

.footer ul li .icon-link {
  display: none;
}

@media screen and (max-width: 425px) {
  .footer ul li .icon-link {
    display: block;
  }
}

.footer ul li .icon-link .linkedin {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  background: url(../images/svg/logo-linkedin.svg) no-repeat;
  background-size: 3rem;
}

.footer ul li .icon-link .github {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  background: url(../images/svg/logo-octocat.svg) no-repeat;
  background-size: 3rem;
}

.footer ul li .icon-link .facebook {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  background: url(../images/svg/logo-facebook.svg) no-repeat;
  background-size: 3rem;
}

.footer ul li .text-link {
  color: #fff;
  transition: color 0.2s;
}

@media screen and (max-width: 425px) {
  .footer ul li .text-link {
    display: none;
  }
}

.footer ul li .text-link:hover {
  color: #9b9b9b;
}
