.header {
  margin: 60px 0;

  @include row-flex();
  justify-content: space-between;


  @include to($max_width: 768px) {
    justify-content: center;


  }

  .portfolio {
    @include col();
    @include size(5);
    @include size-md(12);
  }

  .title {
    @include col();
    @include size(5);
    @include size-md(12);


    border: 3px solid $black-bright;
    width: 100%;
    max-width: 500px;

    text-align: center;

    .name {
      position: relative;
      margin-bottom: 30px;
      font-weight: 500;

      &::before {
        content: '';
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        width: 50px;
        height: 1px;
        bottom: -.70rem;
        background-color: $black-bright;
      }
    }

    .profession {
      font-weight: 400;
      text-transform: capitalize;
      letter-spacing: 0px;
      font-style: italic;
    }
  }
}
